@import"https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700&display=swap";
@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  font-size: 16px!important;
}

img {
  display: inline;
}

.loading-svg [stroke*=FFFFFF] {
  animation: draw 2s infinite;
}

.loading-svg svg{
  width: 150px;
  height: auto;
}
 
 @keyframes draw {
 50% {
    stroke-dashoffset: 0;
  }
}
